          background-color: #2168d1;
          border: none; }
  .create .rc-input-number-input {
    font-family: inherit; }

.separate-middle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center; }



