@font-face {
  font-family: Leafy;
  src: url(./leafy.woff); }

.game .game-container .game-container__actions > button {
  font-family: "Leafy"; }
  .game .game-container .game-container__actions > button:hover, .game .game-container .game-container__actions > button:focus {
    border: none;
    outline: none; }

.game {
  text-align: center;
  font-family: "Leafy"; }
  .game .game-type {
    padding-top: 5%;
    font-size: 50px;
    line-height: 60px; }
  .game .game-timer {
    padding-top: 5%;
    color: #2168d1; }
    .game .game-timer .game-time-seconds {
      font-size: 70px; }
  .game .game-container__word {
    font-size: 70px;
    line-height: 60px;
    margin: 0px;
    margin-top: 20px;
    -webkit-animation: tracking-in-expand 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) both;
            animation: tracking-in-expand 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) both; }
  .game .game-selection {
    padding-top: 90px; }
    .game .game-selection .player-btn {
      line-height: 50px;
      width: 200px;
      font-family: "Leafy";
      font-size: 29px;
      border-radius: 20px; }
  .game .game-container .game-container__actions {
    display: -ms-flexbox;
    display: flex;
    height: 100px; }
    .game .game-container .game-container__actions > button {
      border: none;
      background: transparent;
      font-size: 50px;
      font-weight: bold;
      height: 100%;
      width: 100%; }
      .game .game-container .game-container__actions > button.actor-ready, .game .game-container .game-container__actions > button.ready-state {
        -webkit-animation: wobble-hor-bottom 0.8s infinite;
                animation: wobble-hor-bottom 0.8s infinite; }

.current-score {
  font-size: xx-large; }

@-webkit-keyframes wobble-hor-bottom {
  0%, 100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%; }
  15% {
    -webkit-transform: translateX(-30px) rotate(-6deg);
            transform: translateX(-30px) rotate(-6deg); }
  30% {
    -webkit-transform: translateX(15px) rotate(6deg);
            transform: translateX(15px) rotate(6deg); }
  45% {
    -webkit-transform: translateX(-15px) rotate(-3.6deg);
            transform: translateX(-15px) rotate(-3.6deg); }
  60% {
    -webkit-transform: translateX(9px) rotate(2.4deg);
            transform: translateX(9px) rotate(2.4deg); }
  75% {
    -webkit-transform: translateX(-6px) rotate(-1.2deg);
            transform: translateX(-6px) rotate(-1.2deg); } }

@keyframes wobble-hor-bottom {
  0%, 100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%; }
  15% {
    -webkit-transform: translateX(-30px) rotate(-6deg);
            transform: translateX(-30px) rotate(-6deg); }
  30% {
    -webkit-transform: translateX(15px) rotate(6deg);
            transform: translateX(15px) rotate(6deg); }
  45% {
    -webkit-transform: translateX(-15px) rotate(-3.6deg);
            transform: translateX(-15px) rotate(-3.6deg); }
  60% {
    -webkit-transform: translateX(9px) rotate(2.4deg);
            transform: translateX(9px) rotate(2.4deg); }
  75% {
    -webkit-transform: translateX(-6px) rotate(-1.2deg);
            transform: translateX(-6px) rotate(-1.2deg); } }

@-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -.5em;
    opacity: 0; }
  40% {
    opacity: .6; }
  100% {
    opacity: 1; } }

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -.5em;
    opacity: 0; }
  40% {
    opacity: .6; }
  100% {
    opacity: 1; } }



